import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { PlayPage } from './play';
import { SwingModule } from 'angular2-swing';

@NgModule({
  declarations: [
    PlayPage,
  ],
  imports: [
    IonicPageModule.forChild(PlayPage),
    SwingModule
  ],
  exports: [
    PlayPage
  ]
})
export class PlayPageModule {}



