import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController, LoadingController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import { LoginPage } from "../login/login";
import { ProfilePage } from "../profile/profile";
import { FirstStep3Page } from "../firstStep3/firstStep3";
import { Camera, CameraOptions } from '@ionic-native/camera';

@IonicPage()
@Component({
  selector: 'page-first-step2',
  templateUrl: 'firstStep2.html',
})
export class FirstStep2Page {

  user: any;
  popy: string;
  popyUrl: string;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController, 
    public loadingCtrl: LoadingController ) {
   this.storage.get('isConnected').then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });

   let data: any;
   data = {};

   this.storage.get('user').then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });   
 }

 ionViewDidLoad() {
 }

 close() {
  this.viewCtrl.dismiss();
}

validProfileStep2(){
  this.storage.get('user').then((val) => {
    if (val) {
      let data: any;
      data = {};
      data.popy = this.popy;

      let loading = this.loadingCtrl.create({
        spinner: 'crescent',
        content: 'Etape 2 ...',
        dismissOnPageChange: true
      });

      loading.present();

      this.api.post('addPopy/' + val.id, data)
      .subscribe(
        (data) => {
          let body: any;
          body = JSON.parse(data.text());
          if(body.error){
            loading.dismiss();
            this.doAlert(body.message);
          }else{
            this.navCtrl.setRoot(FirstStep3Page);
          }
        },
        (err) => {
          loading.dismiss();
          this.doAlert(err.message);
                      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                    },
                    () => {
                      //this.goToHome();
                    }
                    );
    }
  });
}

openPhotoPicker() {
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Photo de profil',
    buttons: [
    {
      text: 'Choisir une photo',
      role: 'destructive',
      handler: () => {
        const options: CameraOptions = {
          sourceType: 0,
          quality: 100,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true,
          targetWidth: 2000
        }
        this.camera.getPicture(options).then((imageData) => {
          let base64Image = 'data:image/jpeg;base64,' + imageData;
                          //this.profileImages[index].image = base64Image;
                          this.popy = base64Image;
                          this.popyUrl = 'url(' + base64Image + ')';
                        }, (err) => {
                          // Handle error
                        });
      }
    },
    {
      text: 'Prendre une photo',
      handler: () => {
        const options: CameraOptions = {
          sourceType: 1,
          quality: 100,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true,
          targetWidth: 2000
        }
        this.camera.getPicture(options).then((imageData) => {
                          // imageData is either a base64 encoded string or a file URI
                          // If it's base64 (DATA_URL):
                          let base64Image = 'data:image/jpeg;base64,' + imageData;
                          //this.profileImages[index].image = base64Image;
                          this.popy = base64Image;
                          this.popyUrl = 'url(' + base64Image + ')'

                        }, (err) => {
                          // Handle error
                        });
      }
    },
    {
      text: 'Cancel',
      role: 'cancel',
      handler: () => {
        console.log('Cancel clicked');
      }
    }
    ]
  });

  actionSheet.present();
}

doAlert(message) {
  let alert = this.alertCtrl.create({
    title: 'Aïe!',
    subTitle: message,
    buttons: ['Ok']
  });

  alert.present();
}
}



