import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { Keyboard } from '@ionic-native/keyboard';
import { ImagePicker } from '@ionic-native/image-picker';
import { SwingModule } from 'angular2-swing';

import { MyApp } from './app.component';
import { Settings, Api } from '../providers/providers';
import { ProfilePageModule } from "../pages/profile/profile.module";
import { PlayPageModule } from "../pages/play/play.module";
import { FirstStep1PageModule } from "../pages/firstStep1/firstStep1.module";
import { FirstStep2PageModule } from "../pages/firstStep2/firstStep2.module";
import { FirstStep3PageModule } from "../pages/firstStep3/firstStep3.module";
import { LoginPageModule } from "../pages/login/login.module";
import { Camera } from '@ionic-native/camera';

export function provideSettings(storage: Storage) {

  return new Settings(storage, {
    nbLaunch: 0
  });
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    SwingModule,
    IonicModule.forRoot(MyApp, {
      mode: 'ios',// TODO: to have same iOS look for all platforms
      backButtonText: '',
    }),
    HttpModule,
    IonicStorageModule.forRoot({
      name: '__popmedb'
         //driverOrder: ['localstorage' ]
    }),
    PlayPageModule,
    ProfilePageModule,
    FirstStep1PageModule,
    FirstStep2PageModule,
    FirstStep3PageModule,
    LoginPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    ImagePicker,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Api,
    { provide: Settings, useFactory: provideSettings, deps: [Storage] },
    Camera
  ]
})
export class AppModule {}



