import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import { LoginPage } from "../login/login";
import { ProfilePage } from "../profile/profile";

@IonicPage()
@Component({
  selector: 'page-first-step3',
  templateUrl: 'firstStep3.html',
})
export class FirstStep3Page {

  user: any;
  popy: string;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController, 
    public loadingCtrl: LoadingController ) {
   this.storage.get('isConnected').then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });

   let data: any;
   data = {};

   this.storage.get('user').then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });   
 }

 ionViewDidLoad() {
 }

 close() {
  this.viewCtrl.dismiss();
}

validProfileStep3(){

      let loading = this.loadingCtrl.create({
        spinner: 'crescent',
        content: 'Terminé ...',
        dismissOnPageChange: true
      });
      
      loading.present();

  this.storage.remove('firstConnexion');
  this.navCtrl.setRoot(ProfilePage);
}

doAlert(message) {
  let alert = this.alertCtrl.create({
    title: 'Aïe!',
    subTitle: message,
    buttons: ['Ok']
  });

  alert.present();
}
}



