import { Component } from '@angular/core';
import {
  App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController
} from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";
import { Camera, CameraOptions } from '@ionic-native/camera';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  user: any;
  accountImage: string;
  usualName: string;
  userId: string;
  description: string;
  profileImages: any[4];
  myScore: any;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController ) {
   this.storage.get('isConnected').then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });

   this.storage.get('user').then((val) => {
    if (val) {
      this.loadUserInfo(val.id);
    }
  });   
 }

 loadUserInfo(idUser){
  let loading = this.loadingCtrl.create({
    spinner: 'crescent',
    content: 'Chargement de test données ...',
    dismissOnPageChange: true
  });

  loading.present();

  let data: any;
  data = {};

  this.api.post('getUser/' + idUser, data)
  .subscribe(
    (data) => {
      let body: any;
      body = JSON.parse(data.text());

      this.accountImage =body['account_image'];
      this.userId = body['id'];
      this.usualName = body['usual_name'];
      this.description = body['description'];
      this.profileImages = [{},{},{},{}];
      for(let i=0; i<body['popies'].length;i++){
        this.profileImages[i]=body['popies'][i];
      }

      this.storage.set('user', body);
      this.myScore = body['actual_score'];

      loading.dismiss();
    },
    (err) => {
      this.storage.remove('user');
      this.storage.remove('isConnected');
      this.navCtrl.setRoot(LoginPage);
    },
    () => {
                          //this.goToHome();
                        }
                        );
}

ionViewDidLoad() {
}

close() {
  this.viewCtrl.dismiss();
}

doRefresh(event){
  this.storage.get('user').then((val) => {
    if (val) {
      this.loadUserInfo(val.id);
      event.complete();
    }
  });   
}

openPhotoPicker(index) {
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Modifier votre album',
    buttons: [
    {
      text: 'Choisir une photo',
      role: 'destructive',
      handler: () => {
        console.log('Destructive clicked');
        const options: CameraOptions = {
          sourceType: 0,
          quality: 50,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true
        }
        this.camera.getPicture(options).then((imageData) => {
          let base64Image = 'data:image/jpeg;base64,' + imageData;
          this.profileImages[index].image = base64Image;

          let data: any;
          data = {};
          data.popy = base64Image;

          let loading = this.loadingCtrl.create({
            spinner: 'crescent',
            content: 'Chargement ...',
            dismissOnPageChange: true
          });
          loading.present();

          this.api.post('addPopy/' + this.userId, data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());
              if(body.error){
                loading.dismiss();
                this.doAlert(body.message);
              }else{
                loading.dismiss();
                this.doAlert(body.message);
              }
            },
            (err) => {
              loading.dismiss();
              this.doAlert(err.message);
                        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                      },
                      () => {
                        //this.goToHome();
                      }
                      );






        }, (err) => {
                          // Handle error
                        });
      }
    },
    {
      text: 'Prendre une photo',
      handler: () => {
        console.log('Archive clicked');
        const options: CameraOptions = {
          sourceType: 1,
          quality: 50,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true
        }
        this.camera.getPicture(options).then((imageData) => {
                          // imageData is either a base64 encoded string or a file URI
                          // If it's base64 (DATA_URL):
                          let base64Image = 'data:image/jpeg;base64,' + imageData;
                          this.profileImages[index].image = base64Image;

                          let data: any;
                          data = {};
                          data.popy = base64Image;

                          let loading = this.loadingCtrl.create({
                            spinner: 'crescent',
                            content: 'Chargement ...',
                            dismissOnPageChange: true
                          });
                          loading.present();

                          this.api.post('addPopy/' + this.userId, data)
                          .subscribe(
                            (data) => {
                              let body: any;
                              body = JSON.parse(data.text());
                              if(body.error){
                                loading.dismiss();
                                this.doAlert(body.message);
                              }else{
                                loading.dismiss();
                                this.doAlert(body.message);
                              }
                            },
                            (err) => {
                              loading.dismiss();
                              this.doAlert(err.message);
                                        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                                      },
                                      () => {
                                        //this.goToHome();
                                      }
                                      );

                        }, (err) => {
                          // Handle error
                        });
      }
    },
    {
      text: 'Cancel',
      role: 'cancel',
      handler: () => {
        console.log('Cancel clicked');
      }
    }
    ]
  });

actionSheet.present();


}


removeImage(index) {
  let alert = this.alertCtrl.create({
    title: 'Supprimer cette photo',
    message: 'Tu veux vraiment supprimer cette photo ?',
    buttons: [
    {
      text: 'Annuler',
      role: 'cancel',
      handler: () => {
      }
    },
    {
      text: 'Supprimer',
      handler: () => {
        console.log(this.profileImages[index].id);

        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: 'Suppression ...'
        });
        loading.present();

        let data: any;
        data = {};

        this.api.post('deletePopy/' + this.userId 
         + '/' +this.profileImages[index].id, data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());
            if(body.error){
              loading.dismiss();
              this.doAlert(body.message);
            }else{
              loading.dismiss();
              this.doAlert(body.message);
            }
          },
          (err) => {
            loading.dismiss();
            this.doAlert(err.message);
                      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                    },
                    () => {
                      //this.goToHome();
                    }
                    );
        this.loadUserInfo(this.userId)
        console.log("1");
      }
    }
    ]
  });
  alert.present();
}

goTo(page, direction) {
  this.navCtrl.push(page, {}, {
    direction: direction
  });
}

doAlert(message) {
  let alert = this.alertCtrl.create({
    title: 'Aïe!',
    subTitle: message,
    buttons: ['Ok']
  });

  alert.present();
}

}



